.container {
  width: 50%;
  margin: 40px auto;
}

.small {
  font-size: 0.5em;
}

.btn {
  border: none;
  background-color: #349bff;
  color: #fff;
  padding: 10px 20px;
  border-radius: 3px;
  cursor: pointer;
  margin: 5px;
}

.btn[disabled] {
  background-color: #888;
  cursor: not-allowed;
}

.btn[disabled]:hover {
  -webkit-box-shadow: none;
          box-shadow: none;
}

.btn:hover {
  -webkit-box-shadow: 0 2px 3px 0 #349;
          box-shadow: 0 2px 3px 0 #349;
}

.btn:active {
  -webkit-box-shadow: inset 0 0 3px 0 #fff;
          box-shadow: inset 0 0 3px 0 #fff;
}

.input {
  margin-left: 5px;
  padding: 9px 20px;
  border: 1px solid #349bff;
  color: #349;
}

.btnSubmit {
  margin: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.btnSubmit:hover {
  -webkit-box-shadow: none;
          box-shadow: none;
}


